import React, { Component } from 'react';

function WhitePieces(){
    return [createFirstRow(),createPawns()]
}


function createPawns(){
    const myCells = []
    for(let i = 1 ; i<9 ; i++){
        myCells.push({PositionX: 7, PositionY: i,Name: "Pawn",Type:"White", Data:<i class={"White size fas fa-chess-pawn"}></i> })
    }
    return myCells;
}

function createFirstRow(){
    const myCells = []
    myCells.push({PositionX: 8, PositionY: 1,Name: "Rook",Type:"White", Data:<i class={"White size fas fa-chess-rook"}></i> })
    myCells.push({PositionX: 8, PositionY: 2,Name: "Night",Type:"White", Data:<i class={"White size fas fa-chess-knight"}></i> })
    myCells.push({PositionX: 8, PositionY: 3,Name: "Bishop",Type:"White", Data:<i class={"White size fas fa-chess-bishop"}></i> })
    myCells.push({PositionX: 8, PositionY: 4,Name: "Queen",Type:"White", Data:<i class={"White size fas fa-chess-queen"}></i> })
    myCells.push({PositionX: 8, PositionY: 5,Name: "King",Type:"White", Data:<i class={"White size fas fa-chess-king"}></i> })
    myCells.push({PositionX: 8, PositionY: 6,Name: "Bishop",Type:"White", Data:<i class={"White size fas fa-chess-bishop"}></i> })
    myCells.push({PositionX: 8, PositionY: 7,Name: "Night",Type:"White", Data:<i class={"White size fas fa-chess-knight"}></i> })
    myCells.push({PositionX: 8, PositionY: 8,Name: "Rook",Type:"White", Data:<i class={"White size fas fa-chess-rook"}></i> })

return myCells;
}
export default WhitePieces;