import React, { Component } from 'react';
import Cell from "./Cell"
import createGameBoard,{getAllowedPositions} from "./Helper"
import "./Board.css";

class Board extends Component {
    static defualtProps = {
    }
    state = {
        GameBoard:  createGameBoard(),
        isClicked : false,
        currentPiece: {},
        Allowed: [],
        Turn: "White",
        Direction : "Up",
        GameOver : false,
        Winner: ""

    }

    Reset = () =>{
        this.setState({ GameBoard:  createGameBoard(),
            isClicked : false,
            currentPiece: {},
            Allowed: [],
            Turn: "White",
            Direction : "Up",
            GameOver : false,
            Winner: ""})
    }

    ChangePosition = (e) =>{
        if(!this.state.GameOver){
       console.log(e)
        const isEmpty = !(e.target.outerHTML.substring(0,2) === "<i")
        console.log(isEmpty)
        if(!isEmpty){
            if(!this.state.isClicked || this.state.currentPiece.Type === e.target.parentElement.attributes["Type"].nodeValue){
                if(e.target.parentElement.attributes["Type"].nodeValue === this.state.Turn){
          let  PX = e.target.parentElement.attributes["PX"].nodeValue-1;
          let  PY = e.target.parentElement.attributes["PY"].nodeValue-1;
          let Name = e.target.parentElement.attributes["Name"].nodeValue;
          let Type = e.target.parentElement.attributes["Type"].nodeValue;
          console.log(PX,PY,Name)

         this.setState({Allowed: getAllowedPositions(this.state.GameBoard,Name,PX,PY,Type), 
                        isClicked:true, currentPiece:{Name:Name, PositionX:PX, PositionY:PY, Type:Type}})
         }
        
         }
         else{
                   
            let  PX = e.target.parentElement.attributes["PX"].nodeValue-1;
            let  PY = e.target.parentElement.attributes["PY"].nodeValue-1;
            let Positions = this.state.Allowed;
            let XPiece = Positions[0].PositionX;
            let YPiece = Positions[0].PositionY;
            let isValid = false;
            for(let i = 1 ; i<Positions.length ; i++){
                    if(PX === Positions[i].PositionX && PY === Positions[i].PositionY ) isValid =true;
            }
            if(isValid){
                let newTurn = ""
                let newDirection= this.state.Direction;
                let GameOver = false;
                let Winner = "";
                let Board = this.state.GameBoard;
                if(Board[PX][PY].Name === "Queen"){ 
                    GameOver = true;
                    Winner= this.state.Turn;
                    
                }
                if(!GameOver){
                if(this.state.Turn === "White") {newTurn = "Black";  newDirection = "Down";}
                else{newTurn = "White";  newDirection =  "Up"}
                }
                Board[PX][PY] = {PositionX:PX+1, PositionY:PY+1, Name:Board[XPiece][YPiece].Name,
                                Type:Board[XPiece][YPiece].Type, Data:Board[XPiece][YPiece].Data};
                Board[XPiece][YPiece]= {PositionX:XPiece+1, PositionY: YPiece+1, Name:"",Type:"",Data:""};
                this.setState({GameBoard: Board, isClicked: false,Turn: newTurn,
                                 Allowed:[], Direction: newDirection, GameOver:GameOver, Winner:Winner});
            } 
         }

        }
       else if(this.state.isClicked){
           
            let  PX = e.target.attributes["PX"].nodeValue-1;
            let  PY = e.target.attributes["PY"].nodeValue-1;
            let Positions = this.state.Allowed;
            let XPiece = Positions[0].PositionX;
            let YPiece = Positions[0].PositionY;
            let isValid = false;
            for(let i = 1 ; i<Positions.length ; i++){
                    if(PX === Positions[i].PositionX && PY === Positions[i].PositionY ) isValid =true;
            }
            if(isValid){
                let newTurn = ""
                let newDirection= this.state.Direction;
                let GameOver = false;
                let Winner = "";
                let Board = this.state.GameBoard;
                if(Board[PX][PY].Name === "Queen"){ 
                    GameOver = true;
                    Winner= this.state.Turn;
                    
                }
                if(!GameOver){
                if(this.state.Turn === "White") {newTurn = "Black";  newDirection = "Down";}
                else{newTurn = "White";  newDirection =  "Up"}
                }
                Board[PX][PY] = {PositionX:PX+1, PositionY:PY+1, Name:Board[XPiece][YPiece].Name,
                                Type:Board[XPiece][YPiece].Type, Data:Board[XPiece][YPiece].Data};
                Board[XPiece][YPiece]= {PositionX:XPiece+1, PositionY: YPiece+1, Name:"",Type:"",Data:""};
                this.setState({GameBoard: Board, isClicked: false,Turn:newTurn,
                                Allowed:[],Direction: newDirection,GameOver:GameOver, Winner:Winner});
            }

        }
        
     
    }
    
    }
    render(){

        return(
            <div className="Board">
               
                <h1 style={{color:this.state.Turn}}>
                    {this.state.GameOver?`The Winner is : ${this.state.Winner}`:`${this.state.Turn} Turn`  }
                    </h1>

                <button onClick={this.Reset} className="myButton">Reset</button>
                {this.state.GameOver? <h1 className="GameOver">Game Over</h1> : null}

                <table className= {`${this.state.Direction} Board-table`}>



                    <tr>
                        {this.state.GameBoard[0].map(data =>
                             <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                             onClick={this.ChangePosition} className="odd">{data.Data}</td>)}
                    </tr>

                    <tr>
                    {this.state.GameBoard[1].map(data =>
                         <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                         onClick={this.ChangePosition} className="even">{data.Data}</td>)}

                    </tr>


                    <tr>
                    {this.state.GameBoard[2].map(data =>
                         <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                         onClick={this.ChangePosition} className="odd">{data.Data}</td>)}

                    </tr>

                    <tr>
                    {this.state.GameBoard[3].map(data => 
                         <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                         onClick={this.ChangePosition} className="even">{data.Data}</td>)}

                    </tr>

                    <tr>
                    {this.state.GameBoard[4].map(data => 
                            <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                             onClick={this.ChangePosition} className="odd">{data.Data}</td>)}

                    </tr>

                    <tr>
                    {this.state.GameBoard[5].map(data =>  
                        <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                         onClick={this.ChangePosition} className="even">{data.Data}</td>)}
                    </tr>

                    <tr>
                    {this.state.GameBoard[6].map(data =>
                             <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                             onClick={this.ChangePosition} className="odd">{data.Data}</td>)}

                    </tr>

                    <tr>
                    {this.state.GameBoard[7].map(data =>  
                        <td PX = {data.PositionX}  PY= {data.PositionY} Name = {data.Name} Type ={data.Type}
                         onClick={this.ChangePosition} className="even">{data.Data}</td>)}
                    </tr>

                </table>
            

            </div>

        )
    }
}


export default Board;