import React, { Component } from 'react';

function BlackPieces(){
    return [createFirstRow(),createPawns()]
}


function createPawns(){
    const myCells = []
    for(let i = 1 ; i<9 ; i++){
        myCells.push({PositionX: 2, PositionY: i,Name: "Pawn",Type:"Black", Data:<i class={"Black size fas fa-chess-pawn"}></i> })
    }
    return myCells;
}

function createFirstRow(){
    const myCells = []
    myCells.push({PositionX: 1, PositionY: 1,Name: "Rook",Type:"Black", Data:<i class={"Black size fas fa-chess-rook"}></i> })
    myCells.push({PositionX: 1, PositionY: 2,Name: "Night",Type:"Black", Data:<i class={"Black size fas fa-chess-knight"}></i>})
    myCells.push({PositionX: 1, PositionY: 3,Name: "Bishop",Type:"Black", Data:<i class={"Black size fas fa-chess-bishop"}></i>})
    myCells.push({PositionX: 1, PositionY: 4,Name: "King",Type:"Black", Data:<i class={"Black size fas fa-chess-king"}></i>})
    myCells.push({PositionX: 1, PositionY: 5,Name: "Queen",Type:"Black", Data:<i class={"Black size fas fa-chess-queen"}></i>})
    myCells.push({PositionX: 1, PositionY: 6,Name: "Bishop",Type:"Black", Data:<i class={"Black size fas fa-chess-bishop"}></i>})
    myCells.push({PositionX: 1, PositionY: 7,Name: "Night",Type:"Black", Data:<i class={"Black size fas fa-chess-knight"}></i>})
    myCells.push({PositionX: 1, PositionY: 8,Name: "Rook",Type:"Black", Data:<i class={"Black size fas fa-chess-rook"}></i> })

return myCells;
}
export default BlackPieces;