import BlackPieces from "./BlackPieces"
import WhitePieces from "./WhitePieces"

function createGameBoard(){
    const Black = BlackPieces();
    const White = WhitePieces();
    const myRows = [];
    myRows.push(Black[0])
    myRows.push(Black[1]) 
    let  emptyRow = [];

    for(let i = 3; i<7 ; i++){
        for(let j= 1 ; j<9 ; j++ ){
           
            emptyRow.push({PositionX: i, PositionY: j,Name: "",Type:"", Data:""})
        }
        myRows.push(emptyRow)
        emptyRow = [];
    
    }
    myRows.push(White[1]);
    myRows.push(White[0]);
    return myRows;
}


function getAllowedPositions(Board,Name,PX,PY,Type){
 
    let Allowed = [{PositionX:PX,PositionY:PY}];
    
    //Night
    if(Name === "Night"){
        if( (PX+2<=7 && PY+1<=7) && (Board[PX+2][PY+1].Name === "" || 
        (Type === "Black" && Board[PX+2][PY+1].Type === "White") || (Type === "White" && Board[PX+2][PY+1].Type === "Black")) )
         Allowed.push({PositionX:PX+2, PositionY:PY+1})
        if( (PX+2<=7 && PY-1>=0) && (Board[PX+2][PY-1].Name === "" || 
        (Type === "Black" && Board[PX+2][PY-1].Type === "White") || (Type === "White" && Board[PX+2][PY-1].Type === "Black")) ) 
        Allowed.push({PositionX:PX+2, PositionY:PY-1})
        if( (PX-2>=0 && PY+1<=7) && (Board[PX-2][PY+1].Name === "" || 
        (Type === "Black" && Board[PX-2][PY+1].Type === "White") || (Type === "White" && Board[PX-2][PY+1].Type === "Black")) ) 
        Allowed.push({PositionX:PX-2, PositionY:PY+1})
        if( (PX-2>=0 && PY-1>=0) && (Board[PX-2][PY-1].Name === "" || 
        (Type === "Black" && Board[PX-2][PY-1].Type === "White") || (Type === "White" && Board[PX-2][PY-1].Type === "Black")) )
         Allowed.push({PositionX:PX-2, PositionY:PY-1})
        if( (PX+1<=7 && PY+2<=7) && (Board[PX+1][PY+2].Name === "" || 
        (Type === "Black" && Board[PX+1][PY+2].Type === "White") || (Type === "White" && Board[PX+1][PY+2].Type === "Black")) ) 
        Allowed.push({PositionX:PX+1, PositionY:PY+2})
        if( (PX-1>=0 && PY+2<=7) && (Board[PX-1][PY+2].Name === "" || 
        (Type === "Black" && Board[PX-1][PY+2].Type === "White") || (Type === "White" && Board[PX-1][PY+2].Type === "Black")) )
        Allowed.push({PositionX:PX-1, PositionY:PY+2})
        if( (PX+1<=7 && PY-2>=0) && (Board[PX+1][PY-2].Name === "" || 
        (Type === "Black" && Board[PX+1][PY-2].Type === "White") || (Type === "White" && Board[PX+1][PY-2].Type === "Black")) ) 
        Allowed.push({PositionX:PX+1, PositionY:PY-2})
        if( (PX-1>=0 && PY-2>=0) && (Board[PX-1][PY-2].Name === "" || 
        (Type === "Black" && Board[PX-1][PY-2].Type === "White") || (Type === "White" && Board[PX-1][PY-2].Type === "Black")) )
         Allowed.push({PositionX:PX-1, PositionY:PY-2})
    }

    //Bishop
   else if(Name === "Bishop"){
        let tempX = PX;
        let tempY = PY;
        let Stop = false;
        while(tempX+1 <=7 && tempY+1 <=7 && !Stop ) {
            if(Board[tempX+1][tempY+1].Name === ""){
            Allowed.push({PositionX:tempX+1, PositionY:tempY+1})
            tempX = tempX+1;
            tempY = tempY+1;
            }
            else if((Type === "Black" && Board[tempX+1][tempY+1].Type === "White") || 
            (Type === "White" && Board[tempX+1][tempY+1].Type === "Black")){
                Allowed.push({PositionX:tempX+1, PositionY:tempY+1})
                Stop = true;
            }
            else if(Type === Board[tempX+1][tempY+1].Type)
            Stop = true;
        }

        Stop = false;
        tempX = PX;
        tempY = PY
        while(tempX-1 >=0 && tempY+1 <=7 && !Stop ){
            if(Board[tempX-1][tempY+1].Name === ""){
            Allowed.push({PositionX:tempX-1, PositionY:tempY+1})
            tempX = tempX-1;
            tempY = tempY+1;
            }
            else if((Type === "Black" && Board[tempX-1][tempY+1].Type === "White") || 
            (Type === "White" && Board[tempX-1][tempY+1].Type === "Black")){
                Allowed.push({PositionX:tempX-1, PositionY:tempY+1})
                Stop = true;
            }
            else if(Type === Board[tempX-1][tempY+1].Type)
            Stop = true;
        }
        Stop = false;
        tempX = PX;
        tempY = PY
        while(tempX+1 <=7 && tempY-1 >=0 && !Stop ){
            if(Board[tempX+1][tempY-1].Name === ""){
            Allowed.push({PositionX:tempX+1, PositionY:tempY-1})
            tempX = tempX+1;
            tempY = tempY-1;
            }
            else if((Type === "Black" && Board[tempX+1][tempY-1].Type === "White") || 
            (Type === "White" && Board[tempX+1][tempY-1].Type === "Black")){
                Allowed.push({PositionX:tempX+1, PositionY:tempY-1})
                Stop = true;
            }
            else if(Type === Board[tempX+1][tempY-1].Type)
            Stop = true;
        } 
        Stop = false;
        tempX = PX;
        tempY = PY
        while(tempX-1>=0 && tempY-1>=0 && !Stop  ){
            if(Board[tempX-1][tempY-1].Name === ""){
            Allowed.push({PositionX:tempX-1, PositionY:tempY-1})
            tempX = tempX-1;
            tempY = tempY-1;
            }
            else if((Type === "Black" && Board[tempX-1][tempY-1].Type === "White") || 
            (Type === "White" && Board[tempX-1][tempY-1].Type === "Black")){
                Allowed.push({PositionX:tempX-1, PositionY:tempY-1})
                Stop = true;
            }
            else if(Type === Board[tempX-1][tempY-1].Type)
            Stop = true;

        }
    }

    //Pawn
    else if(Name === "Pawn"){
        if(Type === "Black"){
        if(PX+1 <= 7 && Board[PX+1][PY].Name === "") Allowed.push({PositionX:PX+1, PositionY:PY})
        if(PX+2 <= 7 && PX === 1 && Board[PX+2][PY].Name === "") Allowed.push({PositionX:PX+2, PositionY:PY})
        if(PX+1 <=7 && PY+1<=7 &&  Board[PX+1][PY+1].Type === "White") Allowed.push({PositionX:PX+1, PositionY:PY+1})
        if(PX+1 <=7 && PY-1>=0 &&  Board[PX+1][PY-1].Type === "White") Allowed.push({PositionX:PX+1, PositionY:PY-1})
        }
        else if(Type === "White"){
        if(PX-1 >=0 && Board[PX-1][PY].Name === "") Allowed.push({PositionX:PX-1, PositionY:PY})
        if(PX-2 >=0 && PX === 6 && Board[PX-2][PY].Name === "") Allowed.push({PositionX:PX-2, PositionY:PY})
        if(PX-1 >=0 && PY+1<=7 &&  Board[PX-1][PY+1].Type === "Black") Allowed.push({PositionX:PX-1, PositionY:PY+1})
        if(PX-1 >=0 && PY-1>=0 &&  Board[PX-1][PY-1].Type === "Black") Allowed.push({PositionX:PX-1, PositionY:PY-1})
        }

    }

    //Rook
    else if(Name === "Rook"){
        let Stop = false;
        let tempX = PX;
        let tempY = PY;
        while(tempX+1 <=7 && !Stop ){
            if(Board[tempX+1][tempY].Name === ""){
            Allowed.push({PositionX:tempX+1, PositionY:tempY})
            tempX = tempX+1;
            }
            else if((Type === "Black" && Board[tempX+1][tempY].Type === "White") || 
            (Type === "White" && Board[tempX+1][tempY].Type === "Black")){
                Allowed.push({PositionX:tempX+1, PositionY:tempY})
                Stop = true;
            }
            else if(Type === Board[tempX+1][tempY].Type)
            Stop = true;
        }
        Stop =false;
        tempX = PX;
        while(tempX-1 >=0 &&  !Stop ){
            if(Board[tempX-1][tempY].Name === ""){
            Allowed.push({PositionX:tempX-1, PositionY:tempY})
            tempX = tempX-1;
            }
            else if((Type === "Black" && Board[tempX-1][tempY].Type === "White") || 
            (Type === "White" && Board[tempX-1][tempY].Type === "Black")){
                Allowed.push({PositionX:tempX-1, PositionY:tempY})
                Stop = true;
            }
            else if(Type === Board[tempX-1][tempY].Type)
            Stop = true;

        }
        Stop = false;
        tempX = PX;
        while(tempY+1 <=7 && !Stop ){
            if(Board[tempX][tempY+1].Name === ""){
            Allowed.push({PositionX:tempX, PositionY:tempY+1})
            tempY = tempY+1;
            }
            else if((Type === "Black" && Board[tempX][tempY+1].Type === "White") || 
            (Type === "White" && Board[tempX][tempY+1].Type === "Black")){
                Allowed.push({PositionX:tempX, PositionY:tempY+1})
                Stop = true;
            }
            else if(Type === Board[tempX][tempY+1].Type)
            Stop = true;
        }
        Stop = false;
        tempY = PY;
        while(tempY-1 >=0 && !Stop ){
            if(Board[tempX][tempY-1].Name === ""){
            Allowed.push({PositionX:tempX, PositionY:tempY-1})
            tempY = tempY-1;
            }
            else if((Type === "Black" && Board[tempX][tempY-1].Type === "White") || 
            (Type === "White" && Board[tempX][tempY-1].Type === "Black")){
                Allowed.push({PositionX:tempX, PositionY:tempY-1})
                Stop = true;
            }
            else if(Type === Board[tempX][tempY-1].Type)
            Stop = true;

        }
    }

    else if(Name === "King"){
        if(PX+1 <=7 && (Board[PX+1][PY].Name ===""
        ||(Type === "Black" && Board[PX+1][PY].Type === "White") || (Type === "White" && Board[PX+1][PY].Type === "Black")))  
        Allowed.push({PositionX:PX+1, PositionY:PY})

        if(PX+1 <=7 && PY+1<=7 && (Board[PX+1][PY+1].Name ===""
        ||(Type === "Black" && Board[PX+1][PY+1].Type === "White") || (Type === "White" && Board[PX+1][PY+1].Type === "Black"))) 
        Allowed.push({PositionX:PX+1, PositionY:PY+1})

        if(PX+1 <=7 && PY-1>=0 && (Board[PX+1][PY-1].Name ===""
        ||(Type === "Black" && Board[PX+1][PY-1].Type === "White") || (Type === "White" && Board[PX+1][PY-1].Type === "Black"))) 
        Allowed.push({PositionX:PX+1, PositionY:PY-1})

        if(PX-1 >=0 && (Board[PX-1][PY].Name ===""
        ||(Type === "Black" && Board[PX-1][PY].Type === "White") || (Type === "White" && Board[PX-1][PY].Type === "Black"))) 
        Allowed.push({PositionX:PX-1, PositionY:PY})

        if(PX-1 >=0 && PY+1<=7 && (Board[PX-1][PY+1].Name ===""
        ||(Type === "Black" && Board[PX-1][PY+1].Type === "White") || (Type === "White" && Board[PX-1][PY+1].Type === "Black"))) 
        Allowed.push({PositionX:PX-1, PositionY:PY+1})

        if(PX-1 >=0 && PY-1>=0 && (Board[PX-1][PY-1].Name ==="" 
        ||(Type === "Black" && Board[PX-1][PY-1].Type === "White") || (Type === "White" && Board[PX-1][PY-1].Type === "Black"))) 
        Allowed.push({PositionX:PX-1, PositionY:PY-1})

        if(PY-1 >=0 && (Board[PX][PY-1].Name ===""
        ||(Type === "Black" && Board[PX][PY-1].Type === "White") || (Type === "White" && Board[PX][PY-1].Type === "Black"))) 
        Allowed.push({PositionX:PX, PositionY:PY-1})

        if(PY+1 <=7 && (Board[PX][PY+1].Name ===""
        ||(Type === "Black" && Board[PX][PY+1].Type === "White") || (Type === "White" && Board[PX][PY+1].Type === "Black"))) 
         Allowed.push({PositionX:PX, PositionY:PY+1})


    }
    else if(Name === "Queen"){
        let tempX = PX;
        let tempY = PY;
        let Stop = false;
        while(tempX+1 <=7 && tempY+1 <=7 && !Stop ) {
            if(Board[tempX+1][tempY+1].Name === ""){
            Allowed.push({PositionX:tempX+1, PositionY:tempY+1})
            tempX = tempX+1;
            tempY = tempY+1;
            }
            else if((Type === "Black" && Board[tempX+1][tempY+1].Type === "White") || 
            (Type === "White" && Board[tempX+1][tempY+1].Type === "Black")){
                Allowed.push({PositionX:tempX+1, PositionY:tempY+1})
                Stop = true;
            }
            else if(Type === Board[tempX+1][tempY+1].Type)
            Stop = true;
        }

        Stop = false;
        tempX = PX;
        tempY = PY
        while(tempX-1 >=0 && tempY+1 <=7 && !Stop ){
            if(Board[tempX-1][tempY+1].Name === ""){
            Allowed.push({PositionX:tempX-1, PositionY:tempY+1})
            tempX = tempX-1;
            tempY = tempY+1;
            }
            else if((Type === "Black" && Board[tempX-1][tempY+1].Type === "White") || 
            (Type === "White" && Board[tempX-1][tempY+1].Type === "Black")){
                Allowed.push({PositionX:tempX-1, PositionY:tempY+1})
                Stop = true;
            }
            else if(Type === Board[tempX-1][tempY+1].Type)
            Stop = true;
        }
        Stop = false;
        tempX = PX;
        tempY = PY
        while(tempX+1 <=7 && tempY-1 >=0 && !Stop ){
            if(Board[tempX+1][tempY-1].Name === ""){
            Allowed.push({PositionX:tempX+1, PositionY:tempY-1})
            tempX = tempX+1;
            tempY = tempY-1;
            }
            else if((Type === "Black" && Board[tempX+1][tempY-1].Type === "White") || 
            (Type === "White" && Board[tempX+1][tempY-1].Type === "Black")){
                Allowed.push({PositionX:tempX+1, PositionY:tempY-1})
                Stop = true;
            }
            else if(Type === Board[tempX+1][tempY-1].Type)
            Stop = true;
        } 
        Stop = false;
        tempX = PX;
        tempY = PY
        while(tempX-1>=0 && tempY-1>=0 && !Stop  ){
            if(Board[tempX-1][tempY-1].Name === ""){
            Allowed.push({PositionX:tempX-1, PositionY:tempY-1})
            tempX = tempX-1;
            tempY = tempY-1;
            }
            else if((Type === "Black" && Board[tempX-1][tempY-1].Type === "White") || 
            (Type === "White" && Board[tempX-1][tempY-1].Type === "Black")){
                Allowed.push({PositionX:tempX-1, PositionY:tempY-1})
                Stop = true;
            }
            else if(Type === Board[tempX-1][tempY-1].Type)
            Stop = true;

        }
        Stop = false;
        tempX = PX;
        tempY = PY

        while(tempX+1 <=7 && !Stop ){
            if(Board[tempX+1][tempY].Name === ""){
            Allowed.push({PositionX:tempX+1, PositionY:tempY})
            tempX = tempX+1;
            }
            else if((Type === "Black" && Board[tempX+1][tempY].Type === "White") || 
            (Type === "White" && Board[tempX+1][tempY].Type === "Black")){
                Allowed.push({PositionX:tempX+1, PositionY:tempY})
                Stop = true;
            }
            else if(Type === Board[tempX+1][tempY].Type)
            Stop = true;
        }
        Stop =false;
        tempX = PX;
        while(tempX-1 >=0 &&  !Stop ){
            if(Board[tempX-1][tempY].Name === ""){
            Allowed.push({PositionX:tempX-1, PositionY:tempY})
            tempX = tempX-1;
            }
            else if((Type === "Black" && Board[tempX-1][tempY].Type === "White") || 
            (Type === "White" && Board[tempX-1][tempY].Type === "Black")){
                Allowed.push({PositionX:tempX-1, PositionY:tempY})
                Stop = true;
            }
            else if(Type === Board[tempX-1][tempY].Type)
            Stop = true;

        }
        Stop = false;
        tempX = PX;
        while(tempY+1 <=7 && !Stop ){
            if(Board[tempX][tempY+1].Name === ""){
            Allowed.push({PositionX:tempX, PositionY:tempY+1})
            tempY = tempY+1;
            }
            else if((Type === "Black" && Board[tempX][tempY+1].Type === "White") || 
            (Type === "White" && Board[tempX][tempY+1].Type === "Black")){
                Allowed.push({PositionX:tempX, PositionY:tempY+1})
                Stop = true;
            }
            else if(Type === Board[tempX][tempY+1].Type)
            Stop = true;
        }
        Stop = false;
        tempY = PY;
        while(tempY-1 >=0 && !Stop ){
            if(Board[tempX][tempY-1].Name === ""){
            Allowed.push({PositionX:tempX, PositionY:tempY-1})
            tempY = tempY-1;
            }
            else if((Type === "Black" && Board[tempX][tempY-1].Type === "White") || 
            (Type === "White" && Board[tempX][tempY-1].Type === "Black")){
                Allowed.push({PositionX:tempX, PositionY:tempY-1})
                Stop = true;
            }
            else if(Type === Board[tempX][tempY-1].Type)
            Stop = true;

        }


    }


    return Allowed;
    
}


export default createGameBoard;
export {getAllowedPositions};







